import React, { useEffect } from "react";
import { Environment, useDefaultProgrammingStore } from "../components";
import useMeasure from "react-use-measure";
import basicConfig from "./assets/basicConfig";
import basicStarter from "./assets/basicStarter";
import { ErrorBoundary } from "react-error-boundary";
import ErrorFallback from "./ErrorFallback";

// More on default export: https://storybook.js.org/docs/react/writing-stories/introduction#default-export
export default {
  title: "Fullscreen",
  component: Environment
};
const featured = `
> [error] There was an error!

> [warn] There was an warning related to [The Program](programType)!

> [info] Here is some info!

> [success] Here is a success message!

> Here is a block quote!
`;

// More on component templates: https://storybook.js.org/docs/react/writing-stories/introduction#using-args
const Template = args => {
  const {
    drawers,
    objectTypes,
    programData,
    tabs,
    activeTab,
    executionData,
    drawerWidth,
    ...otherArgs
  } = args;
  const [ref, bounds] = useMeasure();
  useEffect(() => {
    useDefaultProgrammingStore.setState({
      programSpec: {
        drawers,
        objectTypes
      },
      programData,
      executionData,
      featuredDocs: {
        "2dfsessfs": featured
        // "45535153s":true
      },

      tabs,
      activeTab
    });
  }, [drawers, objectTypes, programData, executionData, tabs, activeTab]);
  return <ErrorBoundary FallbackComponent={ErrorFallback}>
      <div ref={ref} style={{
      display: "flex",
      height: "100vh",
      flexDirection: "row",
      backgroundColor: "#333"
    }}>
        <Environment {...otherArgs} store={useDefaultProgrammingStore} height={bounds.height} width={bounds.width} drawerWidth={drawerWidth} />
      </div>
    </ErrorBoundary>;
};
export const Fullscreen = Template.bind({});
// More on args: https://storybook.js.org/docs/react/writing-stories/args
Fullscreen.args = {
  highlightColor: "#ff00ff",
  drawerWidth: 235,
  snapToGrid: true,
  ...basicConfig,
  ...basicStarter
};
Fullscreen.parameters = {
  ...Fullscreen.parameters,
  docs: {
    ...Fullscreen.parameters?.docs,
    source: {
      originalSource: "args => {\n  const {\n    drawers,\n    objectTypes,\n    programData,\n    tabs,\n    activeTab,\n    executionData,\n    drawerWidth,\n    ...otherArgs\n  } = args;\n  const [ref, bounds] = useMeasure();\n  useEffect(() => {\n    useDefaultProgrammingStore.setState({\n      programSpec: {\n        drawers,\n        objectTypes\n      },\n      programData,\n      executionData,\n      featuredDocs: {\n        \"2dfsessfs\": featured\n        // \"45535153s\":true\n      },\n\n      tabs,\n      activeTab\n    });\n  }, [drawers, objectTypes, programData, executionData, tabs, activeTab]);\n  return <ErrorBoundary FallbackComponent={ErrorFallback}>\n      <div ref={ref} style={{\n      display: \"flex\",\n      height: \"100vh\",\n      flexDirection: \"row\",\n      backgroundColor: \"#333\"\n    }}>\n        <Environment {...otherArgs} store={useDefaultProgrammingStore} height={bounds.height} width={bounds.width} drawerWidth={drawerWidth} />\n      </div>\n    </ErrorBoundary>;\n}",
      ...Fullscreen.parameters?.docs?.source
    }
  }
};